.SortInventory {
    max-width: 1260px;
    margin: 0 auto;
    margin-top: 38px;
    padding-top: 40px;
    padding-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

    ul li {
        list-style: none;
    }

    ul {
        padding: 0;
    }

    .categoryList {
        width: 500px;
        display: flex;
        gap: 16px;
        align-items: center;
        margin-right: 30px;

        .categoryItem {
            height: 55px;
            padding: 18px 20px;
            border: 1px solid #872ba1;
            border-radius: 14px;

            text-align: center;
            color: #FFF;
            font-size: 18px;
            font-weight: 600;
            line-height: 16px;
            cursor: pointer;
            transition: .2s ease;

            &:nth-of-type(1), &:nth-of-type(2) {
                width: 126px;
            }

            &:hover {
                border: 1px solid #32166b;
                background: linear-gradient(48deg, #32166b, 50%, #672183 100%);
            }
        }

        .categoryItem.active {
            border: 1px solid #32166b;
            background: linear-gradient(48deg, #32166b, 50%, #672183 100%);
        }
    }

    .SellEverything {
        height: 55px;
        padding: 18px 60px;
        background: linear-gradient(48deg, #b4550a, 50%, #de7c2f 100%);
        border: 1px solid #9b4705;
        border-radius: 14px;

        color: #FFF;
        font-size: 18px;
        font-weight: 400;
        line-height: 18px;
        cursor: pointer;
    }

    .SortInventoryPrice {
        margin-left: 30px;

        .priceList {
            display: flex;
            // border: 1px solid #64597a;
            border-radius: 14px;

            .priceItem {
                height: 55px;
                padding: 18px 20px;
                border: 1px solid #64597a;

                color: #FFF;
                font-size: 15px;
                font-weight: 600;
                line-height: 18px;
                cursor: pointer;
                transition: .2s ease;

                &:hover {
                    border: 1px solid #9830b3;
                    box-shadow: 0 0 10px #9830b3;
                }
            }

            .priceItem:first-child {
                border-top-left-radius: 14px;
                border-bottom-left-radius: 14px;
            }
            .priceItem:last-child {
                border-top-right-radius: 14px;
                border-bottom-right-radius: 14px;
            }
            
            .priceItem.active {
                border: 1px solid #9830b3;
                box-shadow: 0 0 10px #9830b3;
            }
        }
    }
}

@media (max-width: 1300px) {
    .SortInventory {
        flex-wrap: wrap;
        gap: 16px;
        .SortInventoryCategory {
            .categoryList {
                gap: 12px;
                margin-right: 0;
            }
        }
    }
}

@media (max-width: 900px) {
    .SortInventory {
        margin-top: 22px;
        padding-top: 20px;
        padding-bottom: 40px;
        gap: 12px;
        .SortInventoryCategory {
            .categoryList {
                width: 100%;
                gap: 10px;
                .categoryItem {
                    height: auto;
                    padding: 14px 30px;
                    font-size: 16px;
                    line-height: 18px;
                }
            }
        }
        .SellEverything {
            height: auto;
            padding: 14px 30px;
            font-size: 16px;
            line-height: 18px;
        }
        .SortInventoryPrice {
            .priceList {
                .priceItem {
                    height: auto;
                    padding: 14px 25px;
                    font-size: 16px;
                    line-height: 18px;
                }
            }
        }
    }
}
@media(max-width: 710px) {
    .SortInventory {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, 1fr);
        .SellEverything {
            width: 160px;
            margin: 0 auto;
            grid-row: 3;
        }
        .SortInventoryCategory {
            .categoryList {
                justify-content: center;
                .categoryItem {
                    padding: 12px 30px;
                    font-size: 14px;
                    line-height: 16px;
                }
            }
        }
        .SellEverything {
            padding: 12px 30px;
            font-size: 14px;
            line-height: 16px;
        }
        .SortInventoryPrice {
            margin-left: 0;
            .priceList {
                justify-content: center;
                .priceItem {
                    padding: 12px 25px;
                    font-size: 14px;
                    line-height: 16px;
                }
            }
        }
    }
}
@media(max-width:650px) {
    .SortInventory {
        margin-top: 20px;
        padding-top: 18px;
        padding-bottom: 38px;
        .categoryList {
            .categoryItem {
                &:nth-of-type(1), &:nth-of-type(2) {
                    width: 70px;
                }
            }
        }
    }
}

@media(max-width:550px) {
    .SortInventory {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(3, 1fr);
        justify-content: center;
        gap: 10px;
        .SortInventoryCategory {
            .categoryList {
                justify-content: center;
                .categoryItem {
                    padding: 10px 15px;
                    font-size: 10px;
                    line-height: 12px;
                }
            }
        }
        .SellEverything {
            width: 120px;
            font-size: 10px;
            padding: 6px 12px;
        }
        .SortInventoryPrice {
            margin-left: 0;
            .priceList {
                justify-content: center;
                .priceItem {
                    padding: 10px 15px;
                    font-size: 10px;
                    line-height: 12px;
                }
            }
        }
    }
}
@media(max-width: 360px) {
    .SortInventory {
        .SortInventoryPrice {
            .priceList {
                .priceItem {
                    width: 100%;
                    padding: 10px 0;
                    text-align: center;
                }
            }
        }
    }
}